.card {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 200px;
  border: 1px solid #f3f3f3;
  box-sizing: border-box;
  border-radius: 5px;
  padding-bottom: 15px;
}

.card-wrapper {
  margin-top: 50px;
  margin-right: 48px;

  &:nth-child(4n) {
    margin-right: 0;
  }
  &:nth-child(-n+4) {
    margin-top: 0;
  }
}

.card__title,
.card__review,
.card__sku,
.card__price-area {
  flex-basis: 170px;
}

.card__image {
  margin-top: 30px;
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__title {
  margin-top: 45px;
}

.card__title-text {
  margin: 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
}

.card__review {
  padding-top: 15px;
  padding-bottom: 21px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__sku {
  margin-top: 5px;
}

.card__rate {
  margin-top: 15px;
}

.card__price-area {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.card__price-tag {
  padding-bottom: 1px;
}

.card__price {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

.card__button {
  margin-top: 21px;
  display: block;
  background-color: #00acdc;
  text-align: center;
  line-height: 40px;
  height: 40px;
  width: 170px;
  text-transform: uppercase;
  border-radius: 5px;
  font-weight: bold;
  font-size: 14px;
  color: white;
  text-decoration: none;

  &:hover {
    border: 1px solid #00acdc;
    box-sizing: border-box;
    border-radius: 5px;
    background-color: white;
    color: #00acdc;
  }
}

.rate__star {
  display: inline-block;
  background-image: url(../images/star.svg);
  background-position: center;
  background-repeat: no-repeat;
  height: 15px;
  width: 15px;
  margin-right: 4px;
}

.rate {
  display: flex;
}

.stars--4 .rate__star:nth-child(-n+4) {
  background-image: url(../images/star-active.svg);
}
